@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,900;1,100;1,400;1,500;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", arial, sans-serif;
  background-color: #e5e5e5;
}

.component {
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  width: 1440px;
  height: 50vh;
  overflow: hidden;
}
.component > .component-image {
  width: 505px;
  height: 330px;
  margin: 96px 95px 0 230px;
}
.component > .component-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  text-align: left;
  margin: 136px 0 230px 95px;
}
.component > .component-content .component-header {
  font-size: 42px;
  font-weight: bold;
  line-height: 50.4px;
}
.component > .component-content .component-paragraph {
  width: 380px;
  height: 96px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-top: 24px;
}
.component > .component-content .component-button {
  width: 128px;
  height: 48px;
  line-height: 100%;
  margin-top: 32px;
  font-size: 16px;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  background-color: #e4c1ae;
  color: #ffffff;
  cursor: pointer;
}